import React from 'react'
import PropTypes from 'prop-types'
import Img from 'gatsby-image'
import style from './style.module.css'

class ProjectTeaser extends React.Component {
  render() {
    const { content } = this.props
    return (
      <article className={style.container}>
        <section className={style.description}>
          <h1 className={style.title}>{content.title}</h1>
          <p  className={style.copy}>{content.description}</p>
        </section>
        <section className={style.examples}>
          {content.images.map(image => (
            <section className={style.example}>
              <div className={style.imageWrapper} style={{ backgroundColor: image.bgColour }}>
                <Img
                  key={image.id}
                  alt={image.alt}
                  className={'test'}
                  fluid={image.childImageSharp.fluid}
                />
              </div>
              {image.title && (<h2 className={style.subtitle}>{image.title}</h2>)}
            </section>
          ))}
        </section>
      </article>
    )
  }
}

ProjectTeaser.propTypes = {
  content: PropTypes.object,
}

export default ProjectTeaser
